<template>
  <div class="login">
    <div class="words text-center">
      <template v-if="type === 'login'"></template>
      <template v-else-if="type === 'register'"></template>
      <br>,
    </div>
    <el-form ref="form" :model="form" label-width="100px" :rules="rules">
      <el-collapse-transition>
        <div v-if="type === 'login'">
          <el-form-item label="" prop="userName">
            <el-input v-model.trim="form.userName" placeholder="/" :maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model.trim="form.password" placeholder="" type="password" :maxlength="30"></el-input>
          </el-form-item>
        </div>
      </el-collapse-transition>
      <el-collapse-transition>
        <div v-if="type === 'register'">
          <el-form-item label="" prop="userName">
            <el-input v-model.trim="form.userName" placeholder="" :maxlength="14"></el-input>
          </el-form-item>
          <el-form-item label="" prop="email">
            <el-input v-model.trim="form.email" placeholder="" :maxlength="100"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model.trim="form.password" placeholder="" type="password" :maxlength="30"></el-input>
          </el-form-item>
          <el-form-item label="" prop="checked">
            <el-checkbox v-model="form.checked"></el-checkbox>
            <span class="register-declaration" @click="terms = !terms"></span>
            <el-collapse-transition>
              <div v-show="terms" class="terms">
                <div>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                  <br>
                </div>
              </div>
            </el-collapse-transition>
          </el-form-item>
        </div>
      </el-collapse-transition>
      <el-collapse-transition>
        <div v-if="type === 'find-password'">
          <el-form-item label="" prop="email">
            <el-input v-model.trim="form.email" placeholder="" :maxlength="100"></el-input>
          </el-form-item>
        </div>
      </el-collapse-transition>
      <el-form-item label="" label-width="0px">
        <div class="btn-wrap text-center">
          <el-button
            type="primary"
            @click="submit"
            :loading="loading"
            class="submit"
          >{{type === 'register' ?'' :type === 'find-password' ? '':''}}</el-button>
        </div>
      </el-form-item>
    </el-form>
    <div class="text-center">
      <template v-if="type === 'register'">
        <el-button type="text" @click="type = 'login'"></el-button>
      </template>
      <template v-else-if="type === 'login'">
        <el-button type="text" @click="type = 'register'"></el-button>
        <br>
        <el-button type="text" @click="type = 'find-password'"></el-button>
      </template>
    </div>
    <div class="line"></div>
    <div class="third-party">
      <a :href="oauthUrl ? oauthUrl : 'javascript:'">
        <i class="iconfont icon-icon highlight-name"></i>
      </a>
    </div>
  </div>
</template>
<style scoped lang="scss">
.words {
  margin-bottom: 20px;
}

.btn-wrap {
  margin-top: 10px;
}

.submit {
  width: 100%;
}

.register-declaration {
  color: #409eff;
  cursor: pointer;
}

.terms {
  background: #f5f5f5;
  padding: 20px;
  > div {
    max-height: 200px;
    overflow: auto;
    word-break: break-all;
  }
}

.line {
  margin: 20px auto;
  background: #dcdfe6;
  height: 1px;
  width: 80%;
}
.third-party {
  text-align: center;
  a {
    color: inherit;
  }
  i {
    cursor: pointer;
    font-size: 24px;
    transition: all 0.3s;
    &:hover {
      color: #409eff;
    }
  }
}
</style>
<script>
export default {
  props: {
    loginType: {
      default: "",
    },
    done: {
      default: Function,
    },
  },
  watch: {
    loginType(n) {
      if (n) {
        this.type = n;
      }
    },
  },
  data() {
    return {
      form: {
        checked: true,
      },
      oauthUrl: undefined,
      type: this.loginType ? this.loginType : "login",
      terms: false,
      rules: (() => {
        const register = (rule, value, callback) => {
          if (!value) {
            return callback(new Error());
          }
          callback();
        };
        return {
          userName: [
            {
              required: true,
              message:
                this.type === "login" ? "" : "",
              trigger: "change",
            },
            { min: 2, message: "2", trigger: "change" },
          ],
          email: [
            { required: true, message: "", trigger: "blur" },
            {
              type: "email",
              message: "",
              trigger: ["blur", "change"],
            },
          ],
          password: [
            { required: true, message: "", trigger: "change" },
          ],
          checked: [
            {
              required: true,
              message: "",
              trigger: "change",
              validator: register,
            },
          ],
        };
      })(),
      loading: false,
    };
  },
  methods: {
    async submit() {
      await this.$refs.form.validate();
      if (this.type === "register") {
        this.register();
      } else if (this.type === "login") {
        this.login();
      } else if (this.type === "find-password") {
        this.findPassword();
      } else {
        alert("");
      }
    },
    async complete(data) {
      return new Promise((resolve, reject) => {
        resolve(data);
      });
    },
    async login() {
      try {
        this.loading = true;
        const params = Object.assign({}, this.form);
        delete params.checked;
        const data = await this.$ajax.post("/v1/account", params);
        this.$store.dispatch("UserLogin", data);
        if (gtag) {
          gtag("event", "", {
            event_category: data.data.userName,
          });
        }
        this.$message.success(data.message ? data.message : "");
        this.loading = false;
        this.done(data);
        this.$emit("close", "loginVisible", false);
      } catch (e) {
        if (e.message) {
          this.$message.error(e.message);
        }
        this.loading = false;
        throw e;
      }
    },
    async register() {
      try {
        this.loading = true;
        const params = Object.assign({}, this.form);
        delete params.checked;
        const data = await this.$ajax.post("/v1/account/register", params);
        this.$store.dispatch("UserLogin", data);
        this.$message.success(data.message ? data.message : "");
        this.loading = false;
        this.done(data);
        this.$emit("close", "loginVisible", false);
      } catch (e) {
        if (e.message) {
          this.$message.error(e.message);
        }
        this.loading = false;
        throw e;
      }
    },
    async findPassword() {
      try {
        this.loading = true;
        const params = Object.assign({}, this.form);
        delete params.checked;
        const data = await this.$ajax.post("/v1/account/find-password", params);
        this.$store.dispatch("UserLogin", data);
        this.$message.success(
          data.message ? data.message : ""
        );
        this.loading = false;
        this.done(data);
        this.$emit("close", "loginVisible", false);
      } catch (e) {
        if (e.message) {
          this.$message.error(e.message);
        }
        this.loading = false;
        throw e;
      }
    },
  },
  async created() {
    const data = await this.$ajax.get("/v1/account/oauth-url");
    this.oauthUrl = data.url;
  },
};
</script>