<template>
  <el-dialog
    :close-on-press-escape="false"
    :close-on-click-modal="false"
    title=""
    :visible.sync="visible"
    :width="isMobile ? '100%' : '550px'"
    :append-to-body="true"
    :before-close="close"
  >
    <div>
      <el-form ref="form" :model="form" label-width="0px" :rules="rules">
        <el-form-item prop="email">
          <el-input v-model="form.email" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div class="btn-wrap">
        <el-button @click="cancel" size="small"></el-button>
        <el-button type="primary" @click="submit" size="small"></el-button>
      </div>
    </div>
  </el-dialog>
</template>
<style lang="scss" scoped>
.btn-wrap {
  text-align: right;
}
</style>
<script>
export default {
  props: {
    isMobile: {
      default: false
    }
  },
  data() {
    return {
      visible: true,
      input: "",
      form: {
        email: undefined
      },
      rules: {
        email: [
          { required: true, message: "", trigger: "blur" },
          {
            type: "email",
            message: "",
            trigger: ["blur", "change"]
          }
        ]
      }
    };
  },
  methods: {
    async submit() {
      let user = this.$store.state.userInfo;
      if (!user) {
        this.$message.error("");
        return;
      }
      await this.$refs.form.validate();
      let userId = user.id;
      const data = await this.$ajax.post(`v1/users/${userId}/email`, {
        email: this.form.email
      });
      this.$store.dispatch("UserLogin", {
        ...data,
        token: this.$store.state.token
      });
      this.$message.success("");
      this.close();
      this.$emit("close");
    },
    close() {
      this.visible = false;
    },
    cancel() {
      this.close();
      this.$emit("cancel");
    }
  },
  created() {
    let user = this.$store.state.userInfo;
    if (user && user.email) {
      this.form.email = user.email;
    }
  }
};
</script>