let type:{ [key: number]: any } = {
  0:{
    tag:'info',
    text:''
  },
  1:{
    tag:'success',
    text:''
  },
  2:{
    tag:'',
    text:''
  },
  3:{
    tag:'warning',
    text:''
  },
  4:{
    tag:'danger',
    text:''
  },
};


export function tagText(item:any){
  return type[item.rentType].text;
}

export function tagType(item:any){
  return type[item.rentType].tag;
}